

.chapter-cards {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(400px, 400px));
    grid-gap: 20px;
}

.chapter-card {
    display: grid;
    grid-template-rows: repeat(auto-fill, minmax(300px, 300px));;
    background-color: #2f496e;
    box-shadow: 0 4px 8px 0 black;    
}
.chapter-card .content {
    color:white;
    padding: 12px;
    text-align: center;
}

.chapter-card .content>h4{
    font-size: larger;
}
.chapter-card .content>p{
    font-size: medium;
    text-align:justify;
}


.chapter-card .card-footer {
  /* left: 0;
  bottom: 0; */
  width: 100%;
  background-color: var(--main-action-bar-bg-color);
  color: white;
  text-align: center;
}



.chapter-card .card-button:hover {
  opacity: 0.7;
}


.progress-container {
  height: 30px;
  background-color: #ddd;
  border-radius: 15px 15px 15px 15px;
  width: 100%;
}

.progress-bar {
  text-align: right;
  /* padding-right: 10px; */
  color: white;
  border-radius: 15px 15px;
  height: 30px;
}
.progress-done {
  width: 80%; 
  background-color: #2196F3;
}
.chapter-card .card-footer-progress {
  width: 340px;
  height: 40px;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 10px;
  /* background-color: black; */

}

.chapter-cards a {
  text-decoration: none;
}
