.app {
  width: 100%;
  height: 100%;
}

.site-body {
  padding: 1rem;
}

.body-and-sidebar {
  display: grid;
  grid-template-columns: minmax(0,1fr) 300px;
  grid-gap: 4rem;
  max-width: var(--main-max-width);
  margin: 0 auto;
  padding: 2rem 0 2rem;
}

.card {
  transition: 0.3s;
  width: 100%;
  height: 100%;
  border-radius: 5px;
  /* padding: 10px 10px 10px 10px; */

}
.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

