.bottom-nav {
    overflow: hidden;
    background-color:#100e17;
    position: relative;
    bottom: 0;
    width: 100%;
    height: 60px;
    margin-top: 5rem;
  
  }
  
  .bottom-nav a {
    float: left;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 50px;
    text-decoration: none;
    font-size: 17px;
  }
  
  .bottom-nav a:hover {
    background-color: #333333;
    /* color: black; */
  }
  
  
  .bottom-nav-centered a {
    float: none;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  
  .bottom-nav-right {
    float: right;
  }