.site-header-top {
    width: 100%;
    height: 6em;
    background-color: #100e17;
    color: white;
}   

.site-header-top .content {
    padding-left: 5rem;
    padding-top: 1.5rem;
    padding-bottom: 5rem;
    font-weight: 600;
    font-size: 2rem;

}

.site-header-bottom {
    width: 100%;
    height: 3em;
    background-image: linear-gradient(90deg,var(--main-color-orage),#e52e71);
    color: white;
    border-radius: 1px 1px 1px 30px;
}

.site-header-bottom .text{
    color: white;
    font-weight: 600;
    font-size: 1.2rem;
    padding-right: 10rem;
    padding-top: 10px;
    padding-bottom: 10px; 
    float: right;    
}  