.code-box-S {
    height: 12em;
    width: 100%;
}
.grid-container-code-box-S {
    height: 100%;
    width: 100%;  
    display: grid;
    grid-template-columns: 49% 49%;
    grid-template-rows: 23% 75%;
    gap: 10% 2%;
    grid-template-areas: "grid-code-editor grid-action-bar" "grid-code-editor grid-result-box";
  }
  

.code-box-M {
    height: 480px;
    width: 100%;
}
.grid-container-code-box-M {
    height: 100%;
    width: 100%;  
    display: grid;
    grid-template-columns: 49% 49%;
    grid-template-rows: 11% 84%;
    gap: 5% 2%;
    grid-template-areas: "grid-code-editor grid-action-bar" "grid-code-editor grid-result-box";
  }


  .code-box-L {
    height: 600px;
    width: 100%;
}
.grid-container-code-box-L {
    height: 100%;
    width: 100%;  
    display: grid;
    grid-template-columns: 49% 49%;
    grid-template-rows: 9% 88%;
    gap: 3% 2%;
    grid-template-areas: "grid-code-editor grid-action-bar" "grid-code-editor grid-result-box";
  }
 
  .grid-code-editor { grid-area: grid-code-editor; }
  .grid-action-bar { grid-area: grid-action-bar; }
  .grid-result-box { grid-area: grid-result-box; }


.grid-code-editor .card {
    box-shadow: 0 4px 8px 0 black;
    background-color: var(--main-code-block-bg-color);
}
.grid-result-box .card {
    box-shadow: 0 4px 8px 0 black;
    background-color: var(--main-code-block-bg-color);
    background-color: none;
}
.grid-action-bar .card {
    box-shadow: 0 4px 8px 0 black;
    background-color: var(--main-action-bar-bg-color);
}

.inputCodeEditor {
    /* padding: 10px 10px 10px 10px; */
    height: 100%;
    width: 100%;
 }

 .result-box {
    height: 100%;
    width: 100%;
    padding-left: 10px;
    color: white;
    font-family:Arial, Helvetica, sans-serif;
    font-size: 0.75em;    
    line-height: 1.2em;
    margin-top: 0px;
    margin-bottom: 0px;
    overflow: auto;
}

 .text-error {
    color: red;
}
.text-normal {
    color: white; 
}
