.header-nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color:#100e17;
  border-bottom: 2px solid #201c29;
}

.header-nav ul li {
  float: left;
  border-right:1px solid #201c29;
  height: 64px;
}

.header-nav ul li:last-child {
  border-right: none;
}

.header-nav ul li a {
  display: block;
  color: white;
  text-align: center;
  text-decoration: none;
  padding: 10% 50px;
  height: 64px;
}

.header-nav ul li a:hover:not(.active) {
  background-color: #333333;
}

.header-nav ul .navicon {
    padding: 3px 20px ;
}

.header-nav ul .active {
  border-top: 3px solid orange;
}

.action-bar-nav ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.action-bar-nav ul {
  list-style-type: none;
}


ul {
    overflow: hidden;
    list-style-type:square;
    margin-block-start: 3px;
    margin-block-end: 3px;
    margin-inline-start: 1px;
    margin-inline-end: 1px;
  }
  
  .main-nav ul {
      background-color: #100e17;
  }
  
  /* ul li {
    float: left;
  } */
  
  
  
  ul li a {
    
    display: block;
    color: white;
    text-align: center;
    text-decoration: none;
    
    
    font-weight: 400;
    font-family: Ringside Regular A,Ringside Regular B,Rubik,Lato,Lucida Grande,Lucida Sans Unicode,Tahoma,Sans-Serif;
  
  }
  
  .main-nav ul li a {
      border-left: 2px solid #201c29;
      padding-left: 50px;
      padding-right: 50px;
      padding-top: 10%;
      padding-bottom: 10%;
      font-size: 1.1rem;
  }
  
  .action-bar-nav ul li{
    height: 54px;
    float: left;
}

  .action-bar-nav ul li a {
      border-right: 2px solid #201c29;
      padding-left: 50px;
      padding-right: 50px;
      padding-top: 8%;
      padding-bottom: 8%;
      font-size: 0.85rem;
      height: 54px;
  }
  
  .card-footer ul li {
      width: 50%;
      float: left;
  }
  
  .card-footer ul li a {
      border-right: 2px solid #201c29;
      /* padding-left: %; */
      /* padding-right: 20%; */
      padding-top: 8%;
      padding-bottom: 8%;
      font-size: 0.85rem;
      width: 50%;
  }
  
  
  ul li a:hover {
    background-color: #333333;
    color: white;
  }