:root {
  --main-bg-color: white;
  --main-color-orage: #ff8a00;
  --main-txt-color: #333;
  --main-txt-blue-color: #2988bc;
  --main-code-block-bg-color: #002240;
  --main-padding: 15px;  
  --main-maxWidth: 1284px;
  --main-font-size: 1.2rem;
  --main-action-bar-bg-color: #28282a;
  --main-font-family1: medium-content-serif-font, Georgia, Cambria, "Times New Roman", Times, serif;
  --main-font-family2: Sentinel SSm A,Sentinel SSm B,system-ui,-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica,Arial,sans-serif,Apple Color Emoji,Segoe UI Emoji,Segoe UI Symbol;

}

html {
	scroll-behavior: smooth;
}

body {
    font-family:  var(--main-font-family1);
    font-size: var(--main-font-size);
    font-weight: 400;
    background-color: var(--main-bg-color);
    color: var(--main-txt-color);
    line-height: 1.7em;

}

h1 {
    color: var(--main-txt-blue-color);
}
h2 {
    color: var(--main-txt-blue-color);
}
h3 {
    color: var(--main-txt-blue-color);
}

table {
    border-collapse: collapse;
  }
  
  table, th, td {
    border: 0.5px solid #2988bc;
    padding: 8px;
  }

  table th {
    color: var(--main-txt-blue-color);;
    background-color: #f2f2f2;
  }
  tr:nth-child(even) {background-color: #f2f2f2;}

code {
  color: crimson;
}  