.show-hide-box {
    width: 100%;
    padding-top: 30px;
    padding-bottom: 20px;
}
.trigger {
    font-size: 1.4rem;
    color: #2988bc;
}

.trigger input[type="checkbox"] + span {
  display: none;
  opacity: 0;
  -webkit-transition: visibility 0s linear 0.33s, opacity 0.33s linear;
  transition: visibility 0s linear 0.33s, opacity 0.33s linear;
}
.trigger input[type="checkbox"]:checked + span {
  display: block;
  opacity: 1;
  -webkit-transition-delay: 0s;
          transition-delay: 0s;
}

.msg {
  display: block;
  margin-top: 8px;
  padding: 8px 12px;
  border: 1px solid #ddd;
  border-radius: 3px;
  color: var(--main-txt-color);
  font-family: var(--main-font-family1);
  font-size: var(--main-font-size);
  
}

.checkbox {
  position: relative;
  width: 1.2rem;
  height: 1.2rem;
  margin-right: .75rem;
  cursor: pointer;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  outline: 0;
  

}
.checkbox:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  border: 2px solid #a7a9aa;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.checkbox:checked:before {
  height: 50%;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  border-top-style: none;
  border-right-style: none;
}
.checkbox--red:checked:before {
  border-color:orange;
}
